import {types} from 'mobx-state-tree'
import MeditationStore from './MeditationStore'

export const RootStore = types.model({
  meditation: MeditationStore,
})

export type IRootStore = typeof RootStore.Type

export default async function createRootStore(env: any) {
  return RootStore.create({meditation: {}}, env)
}

