import * as React from 'react'
import {View, Text} from 'react-native'
import Button from '../components/Button'
import {Actions} from 'react-native-router-flux'
import {useStore} from '../utils/hooks'

const Result = ({duration = 600, result = 100}: {duration: number; result: number}) => {
  const {meditation} = useStore()!
  const {setFinished} = meditation
  React.useEffect(() => {
    return () => {
      setFinished(false)
    }
  })
  return (
    <View style={{flex: 1, alignItems: 'center'}}>
      <Text style={{paddingTop: 40, color: 'white', fontSize: 20, fontWeight: 'bold'}}>Результат</Text>
      <Text style={{padding: 20, color: 'white', fontSize: 20}}>Длительность: {duration / 60} мин</Text>
      <Text style={{color: 'white', fontSize: 20}}>Ваша внимательность: {result}%</Text>

      <View style={{padding: 40}}>
        <Button
          text="ОК"
          onPress={() => {
            Actions.pop()
          }}
        />
      </View>
    </View>
  )
}

export default Result
