import * as React from 'react'
import {Text, TouchableOpacity} from 'react-native'

const Button = ({onPress, text}: {onPress: () => void; text?: string}) => (
  <TouchableOpacity
    style={{width: 280, height: 60, backgroundColor: '#FFA00A', justifyContent: 'center', alignItems: 'center', borderRadius: 20}}
    onPress={onPress}
  >
    <Text style={{color: 'white', fontWeight: 'bold', fontSize: 20}}>{text || 'Начать'}</Text>
  </TouchableOpacity>
)

export default Button
