import * as React from 'react'
import {observer} from 'mobx-react'
import {View, Text, TouchableOpacity, StyleSheet} from 'react-native'
import {format} from '../utils/format'
import CountdownCircle from '../components/CountdownCircle'
import {useStore} from '../utils/hooks'
import {Actions} from 'react-native-router-flux'

const Meditation = observer(({duration}: {duration: number}) => {
  const {meditation} = useStore()!
  const {failure, finished, started, time, setDuration, setStarted, setHearSound} = meditation
  React.useEffect(() => {
    if (finished) {
      Actions.pop()
      Actions.result({result: Math.round(100 - (failure * 100) / duration), duration})
    } else {
      setDuration(duration)
      setStarted(true)
    }
    return () => {
      setStarted(false)
    }
  }, [finished])

  return (
    <TouchableOpacity
      onPressIn={() => setHearSound(true)}
      onPressOut={() => setHearSound(false)}
      disabled={!started}
      style={{flex: 1, alignItems: 'center', backgroundColor: 'black'}}
    >
      <View style={{alignItems: 'center', justifyContent: 'center'}}>
        <View style={{padding: 50}}>
          {!finished && (
            <CountdownCircle started={started} seconds={duration} radius={125} borderWidth={8} color="#26252A" shadowColor="#FFA00A" bgColor="black">
              <Text style={styles.textStyle}>{format(duration - Math.round(time / 1000))}</Text>
            </CountdownCircle>
          )}
        </View>
      </View>
      <View style={{paddingBottom: 50, alignItems: 'center', justifyContent: 'center'}}>
        <Text style={{fontSize: 16, color: 'white'}}>Нажмите на экран пока Вы слышите звук</Text>
        <Text style={{fontSize: 16, color: 'white'}}>
          Отпустите экран когда звука нет{'\n'}
          {'\n'}
        </Text>
        {/* <Text style={{fontSize: 16, color: 'white'}}>
          Займите удобное положение, расслабьтесь и закройте глаза (когда медитация закончится, Вы услышите двойной звук)
        </Text> */}
      </View>
    </TouchableOpacity>
  )
})

const styles = StyleSheet.create({
  textStyle: {
    alignSelf: 'center',
    fontFamily: 'HelveticaNeue',
    color: 'white',
    fontSize: 60,
    fontWeight: '200',
  },
  myButton: {
    justifyContent: 'center',
    alignItems: 'center',
    padding: 5,
    height: 250,
    width: 250, // The Width must be the same as the height
    borderRadius: 400, // Then Make the Border Radius twice the size of width or Height
    backgroundColor: 'black',
    borderColor: '#FFA00A',
    borderWidth: 10,
  },
})

export default Meditation
