import {Audio} from 'expo-av'
import {INTERRUPTION_MODE_ANDROID_DUCK_OTHERS, INTERRUPTION_MODE_IOS_DUCK_OTHERS} from 'expo-av/build/Audio'

const C5 = require('../../assets/C5.mp3')
const D4 = require('../../assets/D4.mp3')
const F4 = require('../../assets/F4.mp3')
const G4 = require('../../assets/G4.mp3')
const files = [D4, G4, F4, C5]

export const DURATIONS = [15307, 8855, 5459, 6870]

Audio.setAudioModeAsync({
  allowsRecordingIOS: false,
  interruptionModeAndroid: INTERRUPTION_MODE_ANDROID_DUCK_OTHERS,
  interruptionModeIOS: INTERRUPTION_MODE_IOS_DUCK_OTHERS,
  playThroughEarpieceAndroid: true,
  playsInSilentModeIOS: true,
  shouldDuckAndroid: true,
  staysActiveInBackground: false,
})

export class AudioStore {
  sounds: any = []
  async init() {
    this.sounds = []
    for (const file of files) {
      const {sound} = await Audio.Sound.createAsync(file)
      this.sounds.push(sound)
    }
  }

  async destroy() {
    for (let i = 0; i < files.length; i++) {
      this.sounds[i] = undefined
    }
  }
  async playSound(index: number, stop: boolean = false) {
    // const soundObject = new Audio.Sound()
    // await soundObject.loadAsync(files[index])
    // try {
    //   if (stop) {
    //     await soundObject.setPositionAsync(DURATIONS[index] - 100)
    //   }
    //   await soundObject.playAsync()
    //   if (stop) {
    //     await soundObject.pauseAsync()
    //     await soundObject.stopAsync()
    //   }
    // } catch (e) {
    //   alert(e)
    // }

    try {
      if (stop) {
        await this.sounds[index].setPositionAsync(DURATIONS[index] - 100)
      }
      await this.sounds[index].playAsync()
      if (stop) {
        await this.sounds[index].pauseAsync()
        await this.sounds[index].stopAsync()
      }
    } catch (error) {
      alert(error)
    }
  }
}

export default new AudioStore()
