import {types, getEnv} from 'mobx-state-tree'
import {when} from 'mobx'
import {AudioStore} from './AudioStore'

const DURATION = 60
const SILENCE_DURING_SOUND = 60
const SILENCE_WITHOUT_SOUND = 30

const MeditationStore = types
  .model({
    started: false,
    duration: DURATION,
    soundDurations: types.array(types.number),
  })
  .volatile((self) => ({
    time: 0,
    startTime: 0,
    lastSoundTime: 0,
    lastSoundIndex: 0,
    failure: 0,
    hearSound: false,
    finished: false,
  }))
  .actions((self) => ({
    setHearSound(hear: boolean) {
      self.hearSound = hear
    },
    setTime(seconds: number) {
      self.time = seconds
    },
    setFinished(value: boolean) {
      const audioStore: AudioStore = getEnv(self).audioStore
      self.finished = value
      audioStore.destroy()
    },
    setLastSoundTime(time: number) {
      self.lastSoundTime = time
    },
    setLastSoundIndex(index: number) {
      self.lastSoundIndex = index
    },
    resetStats() {
      self.failure = 0
      self.lastSoundIndex = 0
      self.lastSoundTime = 0
      self.finished = false
    },
    increaseFailure() {
      self.failure += 1
    },
  }))
  .actions((self) => {
    let interval: any = null
    let disposer: any = null
    return {
      setSoundDurations(durations: number[]) {
        self.soundDurations.replace(durations)
      },
      setStarted(started: boolean) {
        const audioStore: AudioStore = getEnv(self).audioStore
        self.started = started
        if (started) {
          self.setTime(0)
          self.startTime = Date.now()
          self.resetStats()
          const probsDuringSound: number[] = []
          const probsDuringSilence: number[] = []
          const random: number[] = []
          for (let i = 0; i < SILENCE_DURING_SOUND; i++) {
            probsDuringSound.push(0)
          }
          for (let i = 0; i <= self.duration + 1; i++) {
            random.push(Math.random())
          }
          for (let i = 0; i < SILENCE_WITHOUT_SOUND; i++) {
            probsDuringSilence.push(0)
          }
          for (let i = 0; i < self.soundDurations.length; i++) {
            probsDuringSilence.push(i + 1)
            probsDuringSound.push(i + 1)
          }
          interval = setInterval(() => {
            const now = Date.now()
            self.setTime(now - self.startTime)
            if (self.time > self.duration * 1000) {
              return
            }
            // check if sounds is playing
            const isSound = self.lastSoundTime + self.soundDurations[self.lastSoundIndex - 1] - now
            if (isSound > 0 !== self.hearSound && Math.abs(isSound) > 1000) {
              self.increaseFailure()
              // console.log('ERROR!', isSound, self.hearSound)
            }
            let index = 0
            if (self.lastSoundIndex && now - self.lastSoundTime < 5000) {
              index = 0
            } else if (!self.lastSoundIndex && now - self.startTime > 5000) {
              index = 1 + Math.floor(Math.random() * self.soundDurations.length)
            } else {
              if (self.lastSoundIndex && self.lastSoundTime + self.soundDurations[self.lastSoundIndex - 1] + 4000 > now) {
                index = probsDuringSound[Math.floor(random[Math.floor(self.time / 1000)] * probsDuringSound.length)]
                // if sound has smaller length that previous sound!
                if (self.soundDurations[index - 1] + now < self.lastSoundTime + self.soundDurations[self.lastSoundIndex - 1]) {
                  index = 0
                }
              } else {
                index = probsDuringSilence[Math.floor(random[Math.floor(self.time / 1000)] * probsDuringSilence.length)]
              }
            }
            // reset sound if duration is greater than duration
            if (now - self.startTime + self.soundDurations[index - 1] > self.duration * 1000) {
              index = 0
            }
            if (index) {
              self.setLastSoundTime(now)
              self.setLastSoundIndex(index)
              audioStore.playSound(index - 1)
            }
          }, 1000)
          if (disposer) {
            disposer()
          }
          disposer = when(
            () => self.time > self.duration * 1000,
            () => {
              audioStore.playSound(0)
              audioStore.playSound(1)
              clearInterval(interval)
              ;(self as any).setStarted(false)
              ;(self as any).setFinished(true)
              disposer()
            }
          )
        } else {
          if (interval) {
            clearInterval(interval)
            interval = null
          }
          if (disposer) {
            disposer()
          }
        }
      },
      setDuration(duration: number) {
        self.duration = duration
      },
    }
  })
  .views((self) => ({
    get result(): number {
      return 0
    },
  }))

export default MeditationStore
