import * as React from 'react'
import {useState, useEffect} from 'react'
import createRootStore from './src/stores/RootStore'
import audioStore, {DURATIONS} from './src/stores/AudioStore'
// import {NavigationNativeContainer} from '@react-navigation/native'
// import {createStackNavigator} from '@react-navigation/stack'
import Meditation from './src/screens/Meditation'
import Main from './src/screens/Main'
import Result from './src/screens/Result'
import {StoreContext} from './src/utils/context'
// import * as Localization from 'expo-localization'
// import i18n from 'i18n-js'
import {Router, Stack, Scene} from 'react-native-router-flux'

// const Stack = createStackNavigator()
// Set the key-value pairs for the different languages you want to support.
// i18n.translations = {
//   en: {title: 'Focus - meditation trainer'},
//   ru: {title: 'Focus - тренажер медитации'},
// }
// // Set the locale once at the beginning of your app.
// i18n.locale = Localization.locale
// // When a value is missing from a language it'll fallback to another language with the key present.
// i18n.fallbacks = true

function App() {
  // load store data from local storage
  const [root, setRoot] = useState<any>(undefined)
  useEffect(() => {
    async function loadFromStorage() {
      const store = await createRootStore({audioStore})
      store.meditation.setSoundDurations(DURATIONS)
      setRoot(store)
      // RNBootSplash.hide({duration: 250})
    }
    loadFromStorage()
  }, [])
  return (
    !!root && (
      <StoreContext.Provider value={root}>
        <Router tintColor="white" navigationBarStyle={{backgroundColor: 'black'}} sceneStyle={{backgroundColor: 'black'}}>
          <Stack hideNavBar mode="modal">
            <Stack hideNavBar>
              <Scene key="main" component={Main} />
              <Scene key="meditation" component={Meditation} hideNavBar />
            </Stack>
            <Scene key="result" component={Result} />
          </Stack>
        </Router>
      </StoreContext.Provider>
    )
  )
}

export default App
