import * as React from 'react'
import {View, Text, TouchableOpacity} from 'react-native'
import {Actions} from 'react-native-router-flux'
import AudioStore from '../stores/AudioStore'

const Circle = ({duration = 15, size = 80}: {duration?: number; size?: number}) => (
  <View style={{width: size + 20, height: size + 20, justifyContent: 'center', alignItems: 'center', backgroundColor: 'black'}}>
    <TouchableOpacity
      onPress={async () => {
        await AudioStore.playSound(0, true)
        await AudioStore.playSound(1, true)
        await AudioStore.playSound(2, true)
        await AudioStore.playSound(3, true)
        setTimeout(() => Actions.meditation({duration}))
      }}
      style={{
        borderRadius: (size + 6) / 2,
        backgroundColor: '#222223',
        width: size + 6,
        height: size + 6,
        alignItems: 'center',
        justifyContent: 'center',
      }}
    >
      {/* <Image style={{position: 'absolute', width: size, height: size, tintColor: 'white'}} source={image} /> */}
      <View style={{alignItems: 'center'}}>
        <Text style={{color: 'orange', fontSize: 28, fontWeight: 'bold'}}>{Math.floor(duration / 60)}</Text>
        <Text style={{color: 'white', marginTop: -5, fontSize: 18}}>мин</Text>
      </View>
    </TouchableOpacity>
  </View>
)
export default Circle
